
import React, {useEffect} from 'react';
import HomePage from "../components/HomePage";

function Index({pageTitle}) {

  useEffect(() => {
    return () => {
      let title = `Home Page | ${pageTitle}`;
      if (document.title !== title) document.title = title;
    };
  })

  return (
    <main>
      <HomePage/>
    </main>
  );
}

export default Index;
